.chips_wrp>input ,.Green_wrp>input,.Notattended_wrp>input,.Followup_wrp>input{
	display: none;
}

.chips_wrp>input+label,.Green_wrp>input+label ,.Notattended_wrp>input+label,.Followup_wrp>input+label{
	font-size: 14px;
	font-weight: 400;
	border-radius: 18px !important;
  // color: #f44134;
}

.chips_wrp>input:checked+label {
	text-align: center;
	border-radius: 15px;
	border: 1px solid #b2261c;
	padding: 1px 10px;
	background: rgba(178, 28, 28, 0.141);
	color: #b2261c;
	margin: 5px;
	cursor: pointer;
}
