html {
  height: 100%;
  width: 100%;
}

.pageTitle{
   font-size: 26px;
}

.main_container {
  display: flex;
}

main {
  padding: 10px;
}

.title {
  font-size: 3rem;
  display: grid;
  place-items: center;
}

/* Sidebar */
.sidebar {
  background: rgb(52, 51, 51);
  color: white;
  height: auto;
// position: fixed;
  overflow-y: auto;
   z-index: 999;
}

.AppBar {
  width: 70%;
  // margin-left: 10%;
box-shadow:0px 0px 5px rgba(0, 0, 0, 0.094)
}

.top_section {
  display: flex;
  flex-direction: column;
  align-items: first baseline;
  justify-content: space-between;
 
  /* padding: 15px 10px; */
  /* width: 100%; */
  // z-index: 2;
}

.logo {
  font-size: 18px;
  line-height: 0;
}

.bars {
  width: 30px;
  font-size: 20px;
  cursor: pointer;
}

.hide {
  display: none;
}


.routes {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  // z-index: 10000;
  // padding-left: 10px;
}

.link {
  text-decoration: none !important;
  display: flex;
  gap: 10px;
  padding: 15px 15px;
//  border-top-left-radius: 8px;
//   border-bottom-left-radius: 8px;
  color: white;
  transition: 0.2s cubic-bezier(0.6, -0.28, 0.735, 0.045);
}

.link:hover {
   text-decoration: none;
  // border-top-left-radius: 8px;
  // border-bottom-left-radius: 8px;
  border-right: 5px solid #169c50;
  background: black;
  color: white;
  transition: 0.2s cubic-bezier(0.6, -0.28, 0.735, 0.045);
}


.link_text {
  white-space: nowrap;
  font-size: 15px;
}

.menu {
  display: flex;
  color: white;
  padding: 5px 10px;
  margin-left: 5px;
  // border-right: 4px solid transparent;
  transition: 0.2s cubic-bezier(0.6, -0.28, 0.735, 0.045);
  justify-content: space-between;
}

.menu_item {
  display: flex;
  gap: 10px;
}

.menu_container {
  display: flex;
  flex-direction: column;
}

.menu_container .link {
  padding-left: 20px;
  /* border-bottom: #fff 0.5px solid; */
}

.Notification {
  cursor: pointer;
  padding: 15px;
  font-size: 13px;
  text-align: left;
}
.bars {
    width: 30px;
    font-size: 20px;
    cursor: pointer;
    margin-top: 10px;
    /* z-index: 1; */
  }
@media (max-width: 420px) {
  .pageTitle {
    font-size: 18px;
  }
}